import React from 'react';
import Timestamp from 'react-timestamp';
import Comment from './Comment';




const Blog = () => {


    return (
        <>
        <div className="container">
            <h1 style={{textAlign: "center"}}>Leipzig Celebrates as Bayern Munich wins the Champions League</h1>
            <p>Author: Martin Oputa</p>
    <p>Posted on: {<Timestamp date={"24-August-2020"} /> } </p>

            <div className="container-for-article">
            It was a big celebration in the German city of Leipzig as German club Bayern Munich
             beats PSG 1-0 to win the Champions League.
            German giants Bayern Munich have won the UEFA Champions League for the sixth time in
             the club's history 
            after beating French side Paris Saint-Germain 1-0 at the final in Portugal's capital, Lisbon.
            A goal by 
            French winger Kingsley Coman was enough for the Munich side on Sunday to dash the Parisians'
             dreams of
             winning for the first time the top club competition of European football.s-born Coman,
              a former Paris Saint-Germain player, nodded in at the back post from Joshua Kimmich's 
              cross in the 59th minute. The final was played behind closed doors due
               to the coronavirus pandemic.
            </div>
            <Comment />
        </div>
        </>
    )
};


export default Blog;