import React, { useState } from 'react';
import Timestamp from 'react-timestamp';
import InputBlogData from './InputBlogData';
import Button from './Button';
import Comment from './Comment';




function BlogPosts({author, headline, news}) {
    
      const [blogs, setBlogs] = useState([
            {
                author: "Martin Oputa",
                headline: "What To Do in Leipzig",
                news: "Of all the destinations in Germany I had planned for my recent German trip, Leipzig may have been my most anticipated. Why exactly I don’t know, but I was extremely curious about this city in east Germany. I had a faint recollection of visiting Leipzig as a child but I really didn’t know what was in store for me there. Unlike cities like Berlin and Hamburg, I wasn’t already deeply familiar with the most popular things to do in Leipzig. Just the way I like it.That being said, I had heard good word of mouth about Leipzig from snippets here and there. Arriving eager to learn more, I went straight into tourist mode and came away from it all really appreciating just how much character Leipzig has. It may not be your textbook traditional European city, but with so many sides to it, it’s endlessly interesting. So if you too are curious to explore this enticing destination, here’s what to do in Leipzig to see the different facets of this German gem.",
                id: Math.floor(Math.random() * 2000000),
        },
    {
                author: "Christian Berger",
                headline: "How to Become a Software Developer",
                news: "Software engineering is a field that is vitally important to computer technology as a whole. Without the software to run the computer hardware, that hardware is simply a clump of plastic, silicon and metal – perhaps useful as an overpriced paperweight.In the strictest sense, software engineering is the application of engineering principles to the design, development and implementation of software. Because software engineering is such as a unique, scientific and technically-driven field, special training and formal degrees are required. By getting a degree in software engineering, graduates can work in any number of fields creating video games, developing internet applications, running computer networks or implementing computer security measures for an organization. Find out more about software engineering degrees and careers, and learn about salaries and employment projections for this specialized field.",
                id: Math.floor(Math.random() * 2000000),
    }]);

    const addBlog = (author, headline, news) => {
      setBlogs([...blogs, {author, headline, news, id: Math.floor(Math.random() * 20000)}]);
  };


    
  const AddNewsButton = () => {
    const [showForm, setShowForm] = useState(false)

    const displayOnClick = () => setShowForm(true)

    return (
        <>
        <div className="homepage-button">
            <Button
    type="button" 
    buttonStyle="btn--primary--solid" 
    buttonSize="btn--large" 
    onClick={displayOnClick}
    >
      Click to Add Your News
      </Button>

        </div>
        { showForm ? <InputBlogData addBlog={addBlog} /> : null}
        </>
    )
};



    
      return (
        
        <div>
          
          {blogs.map(blog => (
              <div key={blog.id} className="container">
              <h1 style={{textAlign: "center"}}>{`${blog.headline}`}</h1>
              <p>Author: {`${blog.author}`}</p>
              <p>Posted on: {<Timestamp date={"24-August-2020"} /> } </p>
            
                      <div className="container-for-article">
                      {`${blog.news}`}
                      </div>
                      <Comment />
                  </div>
          ))}
          <AddNewsButton />
      </div> );
  };


export default BlogPosts;