import React, { useState } from 'react';
import Timestamp from 'react-timestamp';

// About this Component
/** This component takes care of the comment section.
 * The comments are stored in a array of objects called "comments", and initialized with 
 * one comment which is saved as "message". This array is objected using "setComments" in React UseState.
 */

const Comment = () => {
    const [comments, setComments] = useState([
        {message: "This is a sample comment. Drop your own comment please!", id: 1},

    ]);

    const [message, setMessage] = useState('');

   const Time = <Timestamp relative date={Date()} relativeTo={Date} autoUpdate/>; 


   const addComment = (message) => {
    setComments([...comments, {message, id: Math.floor(Math.random() * 20000)}]);
};

const handleSubmit = (e) => {
    e.preventDefault();
    addComment(message);
    setMessage('');
    
};

    return (
        
        <div className="commentbox-container">
            <h2>Your Thoughts on this Post</h2>
            <div>
            {comments.map(comment => {
                    return (<p className="actualcomment" key={comment.id}><span>{Time} ago</span>: 
                    &nbsp;{comment.message}</p>);
                })}
            
            </div>
           
            
        <div className="comment-container">
                <h2>Leave Us a Comment</h2>
                <form onSubmit={handleSubmit}>
                    <textarea placeholder='Add Your Comment' 
                    onChange={(e)=> setMessage(e.target.value)} />
                    <div className="btn">
                        <input type="submit" value='Comment' />
                        <button id='clear'>Cancel</button>
                    </div>
                </form>
            </div>
            </div>
    )
};

export default Comment;